import { telegramApiAxios } from '../../../common/utils/interceptor';
import { environment } from '../../../environments/environment';
import { FilteredDefectNumbers } from './DashboardModels';
import { getDefects } from '../defects/DefectsService';
import { User } from '../users/UsersModels';

export async function getUserByName(username: string | null, setSnack): Promise<User> {
    return await telegramApiAxios
        .get(`${environment.BASEURL}user/getByUsername/${username}`)
        .then((res) => {
            if (res.status === 200) {
                return res.data;
            }
        })
        .catch((err) => {
            setSnack({
                open: true,
                message: `    - ${err}`,
                type: 'error',
            });
        });
}

export async function getDefectsNumbers(setSnack): Promise<FilteredDefectNumbers> {
    const AllDefectsArray = await getDefects().then((res) => res.defects);
    if (AllDefectsArray.length <= 0) {
        setSnack({
            open: true,
            message: ` `,
            type: 'info',
        });
        return {
            OpenDefectsCount: 0,
            ProccessDefectsCount: 0,
            ClosedDefectsCount: 0,
        };
    }
    let OpenDefectsCount = 0;
    let ProccessDefectsCount = 0;
    let ClosedDefectsCount = 0;

    for (let item of AllDefectsArray) {
        switch (item.status) {
            case 'open':
                OpenDefectsCount++;
                break;
            case 'fixing':
                ProccessDefectsCount++;
                break;
            case 'solved':
                ClosedDefectsCount++;
                break;
        }
    }
    return {
        OpenDefectsCount,
        ProccessDefectsCount,
        ClosedDefectsCount,
    };
}

export async function getDefectsByDateAndStatus(setSnack, status, date): Promise<any> {
    return await telegramApiAxios
        .get(`${environment.BASEURL}defect/getByDateAndStatus`, {
            params: {
                status,
                date_type: 'open_date',
                start: date,
                end: new Date().toISOString().substr(0, 10),
            },
        })
        .then((res) => {
            return res.data.defects;
        })
        .catch((err) =>
            setSnack({
                open: true,
                message: `    - ${err}`,
                type: 'error',
            }),
        );
}

export async function getDefectsNumbersByDateAndStatus(setSnack, duration): Promise<any> {
    let openDefects;
    let processDefects;
    let closedDefects;
    const Today = new Date();
    const Yesterday = new Date(Today.setDate(Today.getDate() - 1)).toISOString().substr(0, 10);
    const LastWeek = new Date(Today.setDate(Today.getDate() - 7)).toISOString().substr(0, 10);
    const LastMonth = new Date(Today.setDate(Today.getMonth() - 1)).toISOString().substr(0, 10);
    switch (duration) {
        case 'day':
            openDefects = getDefectsByDateAndStatus(setSnack, 'open', Yesterday).then((res) => res);
            processDefects = getDefectsByDateAndStatus(setSnack, 'fixing', Yesterday);
            closedDefects = getDefectsByDateAndStatus(setSnack, 'solved', Yesterday);
            break;
        case 'week':
            openDefects = getDefectsByDateAndStatus(setSnack, 'open', LastWeek).then((res) => res);
            processDefects = getDefectsByDateAndStatus(setSnack, 'fixing', LastWeek).then(
                (res) => res,
            );
            closedDefects = getDefectsByDateAndStatus(setSnack, 'solved', LastWeek).then(
                (res) => res,
            );
            break;
        case 'month':
            openDefects = getDefectsByDateAndStatus(setSnack, 'open', LastMonth).then((res) => res);
            processDefects = getDefectsByDateAndStatus(setSnack, 'fixing', LastMonth).then(
                (res) => res,
            );
            closedDefects = getDefectsByDateAndStatus(setSnack, 'solved', LastMonth).then(
                (res) => res,
            );
            break;
    }
    return await Promise.all([openDefects, processDefects, closedDefects]).then((res) => {
        return {
            OpenDefectsCount: res[0].length,
            ProccessDefectsCount: res[1].length,
            ClosedDefectsCount: res[2].length,
        };
    });
}
