import React, { useEffect, useState } from 'react';
import { Chip } from '@material-ui/core';

function Satus({ status }): JSX.Element {
    const [statusLabel, setStatusLabel] = useState(<Chip label='' color='secondary' />);
    useEffect(() => {
        switch (status.toString()) {
            case 'all':
                setStatusLabel(
                    <Chip label='' style={{ backgroundColor: '#4caf50', color: 'white' }} />,
                );
                break;
            case 'open':
                setStatusLabel(
                    <Chip
                        label=''
                        style={{ backgroundColor: '#4caf50', color: 'white' }}
                    />,
                );
                break;
            case 'fixing':
                setStatusLabel(
                    <Chip
                        label=' '
                        style={{ backgroundColor: '#ff9800', color: 'white' }}
                    />,
                );
                break;
            case 'solved':
                setStatusLabel(<Chip label='' />);
                break;
            case 'true':
                setStatusLabel(
                    <Chip
                        label=''
                        style={{ backgroundColor: '#4caf50', color: 'white' }}
                    />,
                );
                break;
            case 'false':
                setStatusLabel(<Chip label='' />);
                break;
        }
    }, []);
    return statusLabel;
}

export default Satus;
