import { TableCellProps } from '@material-ui/core';

export interface User {
    first_name: string;
    last_name: string;
    username: string;
    enabled: boolean;
}

export interface Defect {
    _id?: string;
    title: string;
    room: string;
    attachment: string;
    user?: User;
    status: string;
    open_date: Date;
}

export interface GetDefects {
    response: string;
    message: string;
    defects: Array<Defect>;
}
interface Columns {
    id: string;
    label: string;
    minWidth?: string;
    align?: TableCellProps['align'];
}
interface StatusFilter {
    title: string;
    value: string;
}
interface PriorityFilter {
    title: string;
    value: number;
}
export const columns: Array<Columns> = [
    { id: 'id', label: '', minWidth: '10%' },
    { id: 'title', label: '', minWidth: '40%' },
    {
        id: 'room',
        label: '',
        minWidth: '15%',
    },
    {
        id: 'status',
        label: '',
        minWidth: '20%',
        align: 'center',
    },
    {
        id: 'operations',
        label: '',
        minWidth: '15%',
        align: 'center',
    },
];

export const statuses: Array<StatusFilter> = [
    { title: ' ', value: 'all' },
    { title: ' ', value: 'open' },
    { title: '  ', value: 'fixing' },
    { title: ' ', value: 'solved' },
];
export const priorities: Array<PriorityFilter> = [
    { title: ' ', value: 0 },
    { title: '', value: 1 },
    { title: '', value: 2 },
    { title: ' ', value: 3 },
    { title: ' ', value: 4 },
];
