import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import {
    Grid,
    Box,
    Card,
    CardHeader,
    CardContent,
    Divider,
    useTheme,
    Typography,
    Button,
} from '@material-ui/core';
import { ArrowRight } from '@material-ui/icons';
import { Bar, Doughnut } from 'react-chartjs-2';

import SnackbarHandler from '../../../common/components/Snackbar/snackbar';
import { getDefectsNumbers, getDefectsNumbersByDateAndStatus } from './DashboardService';
import {
    DoughnutData,
    DoughnutDataOptions,
    BarData,
    BarDataOptions,
} from '../dashboard/DashboardModels';

function Dashboard(): JSX.Element {
    const theme = useTheme();
    const [snack, setSnack] = useState({ open: false, message: '', type: '' });
    const [doughnutData, setDoughnutData] = useState({ datasets: [], labels: [], apperiance: [] });
    const [barData, setBarData] = useState({ datasets: [], labels: [], apperiance: [] } as any);
    const [dataFilter, setDataFilter] = useState('day');

    const doughnutOptions = DoughnutDataOptions(theme);
    const barOptions = BarDataOptions(theme);

    useEffect(() => {
        getDefectsNumbers(setSnack).then((res) =>
            setDoughnutData(
                DoughnutData(
                    res.OpenDefectsCount,
                    res.ProccessDefectsCount,
                    res.ClosedDefectsCount,
                ),
            ),
        );
    }, []);

    useEffect(() => {
        getDefectsNumbersByDateAndStatus(setSnack, dataFilter).then((res) =>
            setBarData(
                BarData(res.OpenDefectsCount, res.ProccessDefectsCount, res.ClosedDefectsCount),
            ),
        );
    }, [dataFilter]);

    return (
        <React.Fragment>
            <Grid container spacing={3}>
                <Grid item lg={8} md={12} xl={9} xs={12}>
                    <Card elevation={3}>
                        <CardHeader
                            action={
                                <React.Fragment>
                                    <Button
                                        onClick={() => setDataFilter('day')}
                                        size='small'
                                        variant='outlined'>
                                         
                                    </Button>
                                    <Button
                                        onClick={() => setDataFilter('week')}
                                        size='small'
                                        variant='outlined'>
                                         
                                    </Button>
                                    <Button
                                        onClick={() => setDataFilter('month')}
                                        size='small'
                                        variant='outlined'>
                                         
                                    </Button>
                                </React.Fragment>
                            }
                            title='   '
                        />
                        <Divider />
                        <CardContent>
                            <Box height={400} position='relative'>
                                <Bar data={barData} options={barOptions} />
                            </Box>
                        </CardContent>
                        <Divider />
                        <Box display='flex' justifyContent='flex-end' p={2}>
                            <Link to='/admin/defects'>
                                <Button
                                    color='primary'
                                    endIcon={<ArrowRight />}
                                    size='small'
                                    variant='text'>
                                    
                                </Button>
                            </Link>
                        </Box>
                    </Card>
                </Grid>
                <Grid item lg={4} md={6} xl={3} xs={12}>
                    <Card>
                        <CardHeader title='  ' />
                        <Divider />
                        <CardContent>
                            <Box height={300} position='relative'>
                                <Doughnut data={doughnutData} options={doughnutOptions} />
                            </Box>
                            <Box display='flex' justifyContent='center' mt={2}>
                                {doughnutData.apperiance.map(({ color, icon, title, value }) => (
                                    <Box key={title} p={1} textAlign='center'>
                                        {icon}
                                        <Typography color='textPrimary' variant='body1'>
                                            {title}
                                        </Typography>
                                        <Typography style={{ color }} variant='h2'>
                                            {value}
                                        </Typography>
                                    </Box>
                                ))}
                            </Box>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
            <SnackbarHandler snack={snack} setSnack={setSnack} />
        </React.Fragment>
    );
}

export default Dashboard;
